import { Component, OnInit } from '@angular/core';

import { SubjectService } from './../subject.service';

import { Subject } from './../subject';
import { Task } from './../task';

@Component({
	selector: 'app-todo-page',
	templateUrl: './todo-page.component.html',
	styleUrls: ['./todo-page.component.scss']
})

export class TodoPageComponent implements OnInit {

	taskToBePushed: string = "";

	dateString: string = "";

	subjects: Object[] = [];

	constructor(
		private subjectService: SubjectService
	) {}

	ngOnInit(): void {
		this.subjectService.getSubjects()
			.then(subjects => this.subjects = subjects);

		// Initialise dateString with today's date.
		this.resetDateInput();
	}

	removeSelected() {
		for (let subject of this.subjects) {
			for (let task of subject['tasks']) {
				if(task['completed']) {
					var index = subject['tasks'].indexOf(task);
					subject['tasks'].splice(index,1);
				}
			}
		}
	}

	anySelected(): boolean {
		for (let subject of this.subjects) {
			for (let task of subject['tasks']) {
				if(task['completed']) {
					return true;
				}
			}
		}
		return false;
	}

	pushTask(subject: Subject) {
		var index = this.subjects.indexOf(subject);

		// In form yyyy-mm-dd
	  	var yyyy = this.dateString.slice(0,4);
	  	var mm = this.dateString.slice(5,7);
	  	var dd = this.dateString.slice(8,10);
		
		this.subjects[index]['tasks'].push(new Task(this.taskToBePushed,dd,mm,yyyy,this.subjectService.getDateFormat()));
	  	this.taskToBePushed = "";
	  	// Close subject.
	  	this.subjects[index]['open'] = false;	

	  	// Order list for this subject.
	  	this.orderList(index);  	
	  	this.resetDateInput();
	}

	resetDateInput() {
		var today = new Date();
		var dd = today.getDate();
		var mm = today.getMonth()+1; // January is 0.
		var yyyy = today.getFullYear();

		var ddStr = dd.toString();
		var mmStr = mm.toString();
		var yyyyStr = yyyy.toString();

		if(dd < 10) {
		    ddStr="0"+dd;
		} 

		if(mm < 10) {
		    mmStr="0"+mm;
		}
		this.dateString = yyyyStr+"-"+mmStr+"-"+ddStr;
	}

	orderList(index: number) {
		this.subjects[index]['tasks'].sort(function(a,b) {
			var aDueYear = +a.dueYear;
			var aDueMonth = +a.dueMonth;
			var aDueDay = +a.dueDay;
			var bDueYear = +b.dueYear;
			var bDueMonth = +b.dueMonth;
			var bDueDay = +b.dueDay;

			if(aDueYear > bDueYear) {
				return 1;
			}
			else if(aDueYear < bDueYear) {
				return -1;
			}
			else {
				if(aDueMonth > bDueMonth) {
					return 1;
				}
				else if(aDueMonth < bDueMonth) {
					return -1;
				}
				else {
					if(aDueDay > bDueDay) {
						return 1;
					}
					else if(aDueDay < bDueDay) {
						return -1;
					}
					else {
						return 0;
					}
				}
			}
		});
	}

	toggleInput(subject: Object): void {
		var index = this.subjects.indexOf(subject);
		var count = 0;
		if(index == -1) {
			console.log("subject object not found in list");
			return;
		}
		if(subject['open'] == true) {
			subject['open'] = false;
		}
		else {
			subject['open'] = true;
			// Close all other subject tabs.
			for (let subject of this.subjects) {
				if(count != index) {
					subject['open'] = false;
				}
				count++;
			}
		}
		this.taskToBePushed = "";
	}

	addNewSubject(): void {
		var sub = new Subject("adding")
		this.subjects.push(sub);
	}

}



