import { Component, OnInit } 						from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } 	from '@angular/router';

import 'rxjs/add/operator/filter';

const ROUTES = [
	{routerLink: "home", 		name: "Home" },
	{routerLink: "schedule", 	name: "Schedule" },
	{routerLink: "todo", 		name: "Todo List" },
	{routerLink: "subject-list",name: "Subject List"},
	{routerLink: "settings",	name: "Settings" }
];

@Component({
 	selector: 'app-root',
  	templateUrl: './app.component.html',
  	styleUrls: ['./app.component.scss']
})

export class AppComponent implements OnInit {

	title: string;

	routeList: Array<any> = ROUTES;

	ngOnInit() {
		this.router.events
	    .filter(event => event instanceof NavigationEnd)
	    .subscribe(event => {
	      	let currentRoute = this.route.root;
	      	while (currentRoute.children[0] !== undefined) {
	        	currentRoute = currentRoute.children[0];
	      	}
	      	this.title = currentRoute.snapshot.data['name'];
	    })
	}

	constructor(
	  	private route: ActivatedRoute,
	  	private router: Router
	) {}
}



