import { Injectable } from '@angular/core';

@Injectable()
export class SettingsService {
	dateFormat: string = "dd/mm/yy";

  	constructor() {}

  	getDateFormat(): string {
  		return this.dateFormat;
  	}

  	setDateFormat(format: string): void {
  		this.dateFormat = format;
  	}

}



