import { Injectable, OnInit } from '@angular/core';

import { Subject } from './subject';

import { SettingsService } from './settings.service';

const SUBJECTS: Subject[] = [
	new Subject("MATH1210"),
	new Subject("PHYS1210")
];

@Injectable()
export class SubjectService implements OnInit {

	currentDateFormat: string = "dd/mm/yy";

	ngOnInit() {

	}

  	constructor(
  		private settingsService: SettingsService
  	) { }

  	getSubjects() : Promise<Subject[]> {
		return Promise.resolve(SUBJECTS);
	}

	setDateFormat(format: string) {
		this.currentDateFormat = format;

		// Set all to this new format.
		for (let subject of SUBJECTS) {
			for (let task of subject['tasks']) {
				switch(format) {
		    		case 'dd/mm/yyyy':
		    			task.formatddmmyyyy();
		    			break;
		    		case 'mm/dd/yyyy':
		    			task.formatmmddyyyy();
		    			break;	
		    		case 'dd/mm/yy':
		    			task.formatddmmyy();
		    			break;
		    		case 'mm/dd/yy':
		    			task.formatmmddyy();
		    			break;
		    		case 'dd/mm':
		    			task.formatddmm();
		    			break;
		    		case 'mm/dd':
		    			task.formatmmdd();
		    	}
			}
		}
	}

	getDateFormat(): string {
		return this.currentDateFormat;
	}

	// getSubject(id: number) : Promise<Subject> {
	// 	return this.getSubjects()
	// 			.then(tasks => tasks.find(task => task.id === id))
	// }

}



