import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { MdDialog } from '@angular/material';

import { Subject } from './../subject';
import { DialogConfirmComponent } from './../dialog-confirm/dialog-confirm.component';

import { SubjectService } from './../subject.service';
import { DialogService }	from './../dialog.service';

@Component({
  selector: 'app-subject-list-page',
  templateUrl: './subject-list-page.component.html',
  styleUrls: ['./subject-list-page.component.scss']
})
export class SubjectListPageComponent implements OnInit {

	adding: boolean = false;
	subjectToBeAdded: string = "";

	subjects: Object[] = [];

  	constructor(
  		private subjectService: SubjectService,
  		private dialogsService: DialogService, 
    	private viewContainerRef: ViewContainerRef
  	) { }

  	ngOnInit(): void {
		this.subjectService.getSubjects()
			.then(subjects => this.subjects = subjects);
	}

	toggleAdding() {
		if(this.adding) {
			this.adding = false;
		}
		else {
			this.adding = true;
		}
	}

	addSubject() {
		this.subjects.push(new Subject(this.subjectToBeAdded));
		this.subjectToBeAdded = "";
		this.toggleAdding();
	}

	removeSubject(subject) {
		var result: any;
		this.dialogsService
      		.confirm('Confirmation', 'Are you sure you want to delete '+subject['name']+"?", this.viewContainerRef)
      		.subscribe(res => result = res);
      	this.dialogsService.setSubject(subject);
	}

}


