import { NgModule }             	from '@angular/core';
import { RouterModule, Routes } 	from '@angular/router';

import { HomePageComponent }		from './home-page/home-page.component';
import { NotFoundPageComponent }	from './not-found-page/not-found-page.component';
import { SchedulePageComponent } 	from './schedule-page/schedule-page.component';
import { TodoPageComponent }		from './todo-page/todo-page.component';
import { SettingsPageComponent } 	from './settings-page/settings-page.component';
import { SubjectListPageComponent } from './subject-list-page/subject-list-page.component';

const routes: Routes = [
  	{ path: '', redirectTo: '/home', pathMatch: 'full', 	data: {name: 'UniBuddy'} },
  	{ path: 'home', 	component: HomePageComponent, 		data: {name: 'UniBuddy'} },
  	{ path: 'schedule',	component: SchedulePageComponent, 	data: {name: 'Schedule'} },
  	{ path: 'todo',		component: TodoPageComponent, 		data: {name: 'Todo List'} },
  	{ path: 'subject-list',	component: SubjectListPageComponent, 	data: {name: 'Subject List'} },
  	{ path: 'settings',	component: SettingsPageComponent, 	data: {name: 'Settings'} },
  	{ path: '**',		component: NotFoundPageComponent, 	data: {name: 'ERROR'} }
];

@NgModule({
  	imports: [ RouterModule.forRoot(routes) ],
  	exports: [ RouterModule ]
})

export class AppRoutingModule {}


